import React, { Component } from "react";
import { BsArrowLeft } from "react-icons/bs";
import { ImRadioChecked, ImRadioUnchecked } from "react-icons/im";
import {
  DPMembershipApplicationGetInterface,
  DPMembershipApplicationStatus,
} from "../../actions/dp_membership_application";
import Button from "../../components/Buttons/Button";
import PdfViewer from "../../components/PdfViewer/PdfViewer";
import {
  IMPLEMENTATION_PLANS_PATH,
  RGB_CERTIFICATES_PATH,
} from "../../config/app.config";

interface DPApplicationDetailsProps {
  selected_status: DPMembershipApplicationStatus;
  data: DPMembershipApplicationGetInterface;
  onClose: () => void;
  openValidation: () => void;
  onRegisterAccount: (data: DPMembershipApplicationGetInterface) => void;
  onViewAccountsList: (data: DPMembershipApplicationGetInterface) => void;
}
interface DPApplicationDetailsState {
  opened_document: "RGB" | "IMPLEMENTATION" | null;
  loading_file: boolean;
}
export class DPApplicationDetails extends Component<
  DPApplicationDetailsProps,
  DPApplicationDetailsState
> {
  constructor(props: DPApplicationDetailsProps) {
    super(props);

    this.state = {
      opened_document: null,
      loading_file: true,
    };
  }

  GetFileName = () => {
    return this.state.opened_document === "RGB"
      ? RGB_CERTIFICATES_PATH + this.props.data.rgb_certificate_file_name
      : IMPLEMENTATION_PLANS_PATH +
          this.props.data.implementation_plan_file_name;
  };

  render() {
    const details = this.props.data;

    return (
      <div className="p-3">
        <div className="flex flex-row items-center justify-between gap-2 border-b pb-4">
          <div className="bg-white rounded-md flex flex-row items-center gap-3">
            <div
              onClick={this.props.onClose}
              className="h-10 w-10 rounded-full bg-accent-400 hover:bg-accent-500 text-accent-900 hover:text-black flex items-center justify-center cursor-pointer"
            >
              <BsArrowLeft className="text-2xl" />
            </div>
            <div>
              <div className="text-xl font-bold">
                {details.development_partner_name}
              </div>
              <div className="text-accent-900 -mt-1 bg-accent-400 rounded-md w-max px-1 py-0">
                {details.organization_category}
              </div>
            </div>
          </div>
          <div className="flex flex-row justify-end gap-2">
            <div className="flex flex-col items-end">
              <div className="text-sm px-2">Status</div>
              <div className="bg-accent-400 text-accent-900 font-bold text-sm px-2 rounded-md">
                {this.props.selected_status}
              </div>
            </div>
          </div>
        </div>
        <div>
          <div className=" rounded-md mt-5">
            <div className="text-lg font-bold">Info Details</div>
            <div className="grid grid-cols-12 mt-2 gap-2">
              <div className="col-span-12 md:col-span-6 lg:col-span-4 flex flex-col bg-gray-100 rounded-md px-3 py-2">
                <div className="text-gray-600 text-sm">
                  Development Partner name
                </div>
                <div className="-mt-0">{details.development_partner_name}</div>
              </div>
              <div className="col-span-12 md:col-span-6 lg:col-span-4 flex flex-col bg-gray-100 rounded-md px-3 py-2">
                <div className="text-gray-600 text-sm">
                  Organization category
                </div>
                <div className="-mt-0">{details.organization_category}</div>
              </div>
              <div className="col-span-12 md:col-span-6 lg:col-span-4 flex flex-col bg-gray-100 rounded-md px-3 py-2">
                <div className="text-gray-600 text-sm">
                  Organization TIN number
                </div>
                <div className="-mt-0">{details.tin_number}</div>
              </div>
              <div className="col-span-12 md:col-span-6 lg:col-span-4 flex flex-col bg-gray-100 rounded-md px-3 py-2">
                <div className="text-gray-600 text-sm">Organization email</div>
                <div className="-mt-0">{details.email}</div>
              </div>
              <div className="col-span-12 md:col-span-6 lg:col-span-4 flex flex-col bg-gray-100 rounded-md px-3 py-2">
                <div className="text-gray-600 text-sm">
                  Organization phone number
                </div>
                <div className="-mt-0">{details.phone}</div>
              </div>
              <div className="col-span-12 md:col-span-6 lg:col-span-4 flex flex-col bg-gray-100 rounded-md px-3 py-2">
                <div className="text-gray-600 text-sm">
                  Organization website
                </div>
                <div className="-mt-0">{details.website_link}</div>
              </div>
              <div className="col-span-12 md:col-span-6 lg:col-span-4 flex flex-col bg-gray-100 rounded-md px-3 py-2">
                <div className="text-gray-600 text-sm">
                  Organization location
                </div>
                <div className="-mt-0">{details.location}</div>
              </div>
              {details.comment !== "" && details.comment !== null && (
                <div className="col-span-12 md:col-span-8 flex flex-col bg-gray-100 rounded-md px-3 py-2">
                  <div className="text-gray-600 text-sm">
                    Validation comment
                  </div>
                  <div className="-mt-0">{details.comment}</div>
                </div>
              )}
            </div>
          </div>
          {/* Documents */}
          <div className="mt-6">
            <div className="font-bold mb-3 text-lg">Documents attached</div>
            <div className="flex flex-col md:flex-row items-center gap-2">
              <div
                onClick={() => this.setState({ opened_document: "RGB" })}
                className={`px-2 pr-4 py-2 rounded-md cursor-pointer ${
                  this.state.opened_document === "RGB"
                    ? "bg-accent-400 text-accent-900"
                    : "bg-gray-100 hover:bg-accent-400"
                } flex flex-row items-center justify-center gap-2`}
              >
                <div>
                  {this.state.opened_document === "RGB" ? (
                    <ImRadioChecked className="text-2xl animate__animated animate__zoomIn animate__faster" />
                  ) : (
                    <ImRadioUnchecked className="text-2xl" />
                  )}
                </div>
                <span>RGB Certificate document</span>
              </div>
              <div
                onClick={() =>
                  this.setState({ opened_document: "IMPLEMENTATION" })
                }
                className={`px-2 pr-4 py-2 rounded-md cursor-pointer ${
                  this.state.opened_document === "IMPLEMENTATION"
                    ? "bg-accent-400 text-accent-900"
                    : "bg-gray-100 hover:bg-accent-400"
                } flex flex-row items-center justify-center gap-2`}
              >
                <div>
                  {this.state.opened_document === "IMPLEMENTATION" ? (
                    <ImRadioChecked className="text-2xl animate__animated animate__zoomIn animate__faster" />
                  ) : (
                    <ImRadioUnchecked className="text-2xl" />
                  )}
                </div>
                <span>Implementation document</span>
              </div>
            </div>
            {this.state.opened_document !== null && (
              <div className="bg-gray-100 rounded-md overflow-hidden mt-1">
                {this.GetFileName().slice(-3) === "pdf" ? (
                  <div>
                    {/* {this.state.loading_file === true && (
                      <div className="flex flex-col py-5 items-center justify-center">
                        <Loading />
                        <div className="text-center">
                          Loading file, please wait...
                        </div>
                      </div>
                    )} */}
                    <PdfViewer
                      file_url={`${this.GetFileName()}`}
                      class_name={"w-full h-100vh"}
                      frame_title={"User document"}
                      setLoadingFile={(status: boolean) => {
                        this.setState({ loading_file: status });
                      }}
                      full_height={true}
                    />
                  </div>
                ) : (
                  <img
                    src={this.GetFileName()}
                    alt={"File"}
                    className="w-full h-auto"
                  />
                )}
              </div>
            )}
          </div>
          {/* Sub areas */}
          <div className="mt-10">
            <div className="font-bold mb-3 text-lg">
              Areas of intervention{" "}
              <span className="px-1 rounded-md bg-accent-400 text-accent-900 text-sm font-bold">
                Total: {details.subareas.length}
              </span>
            </div>
            <div className="grid grid-cols-12">
              {details.subareas.map((item, i) => (
                <div
                  key={i + 1}
                  className="col-span-12 flex flex-row items-center gap-2 px-2 py-1 rounded-md bg-gray-100 mb-2 group hover:bg-accent-400 hover:text-accent-900 cursor-pointer"
                >
                  <div>
                    <ImRadioChecked className="text-2xl text-gray-300 group-hover:text-accent-900" />
                  </div>
                  <div className="flex flex-col">
                    <div className="">{item.subarea_name}</div>
                    <div className="text-sm -mt-1 text-gray-500 group-hover:text-accent-900">
                      {item.area_name}
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>
        {/* Footer */}
        <div className="mt-3 flex flex-row items-center gap-2 justify-end">
          <Button onClick={this.props.onClose} theme="default">
            <span>Back to list</span>
          </Button>
          {(this.props.selected_status.toLowerCase() ===
            DPMembershipApplicationStatus.PENDING.toLowerCase() ||
            this.props.selected_status.toLowerCase() ===
              DPMembershipApplicationStatus.APPEAL.toLowerCase()) && (
            <Button onClick={this.props.openValidation} theme="accent">
              <span>Validate registration</span>
            </Button>
          )}
          {this.props.selected_status.toLocaleLowerCase() ===
            DPMembershipApplicationStatus.APPROVED.toLocaleLowerCase() && (
            <div className="flex flex-row items-center gap-2">
              <Button
                onClick={() => this.props.onViewAccountsList(this.props.data)}
                theme="accent-light"
              >
                <span>View accounts</span>
              </Button>
              <Button
                onClick={() => this.props.onRegisterAccount(this.props.data)}
                theme="accent"
              >
                <span>Register account</span>
              </Button>
            </div>
          )}
        </div>
      </div>
    );
  }
}
