import React, { Component } from "react";
import { BsArrowLeft } from "react-icons/bs";
import {
  DPAccountInterface,
  DPMembershipApplicationGetInterface,
} from "../../actions/dp_membership_application";
import Button from "../../components/Buttons/Button";
import Loading from "../../components/Loading/Loading";
import { search } from "../../utils/functions";

interface DPAccountsListProps {
  selected_development_partner: DPMembershipApplicationGetInterface;
  onCancel: () => void;
  FC_GetAccounts: (id: string) => void;
  accounts: DPAccountInterface[] | null;
  onAddNewAccount: () => void;
  loading: boolean;
}
interface DPAccountsListState {
  loading: boolean;
  error: string;
  success: string;
  search_data: string;
}

export class DPAccountsList extends Component<
  DPAccountsListProps,
  DPAccountsListState
> {
  constructor(props: DPAccountsListProps) {
    super(props);

    this.state = {
      loading: false,
      error: "",
      success: "",
      search_data: "",
    };
  }

  componentDidMount(): void {
    if (this.props.accounts === null) {
      this.props.FC_GetAccounts(this.props.selected_development_partner.id);
    }
  }
  render() {
    return (
      <div>
        <div className="bg-white p-2 rounded">
          <div className="bg-gray-100 py-1 flex flex-row items-center gap-2 rounded px-2 animate__animated animate__fadeIn">
            <div>
              <div
                onClick={() => {
                  this.state.loading === false && this.props.onCancel();
                }}
                className="bg-white rounded-full flex items-center justify-center h-9 w-9 cursor-pointer hover:bg-accent-900 hover:text-white"
              >
                <BsArrowLeft className="text-2xl" />
              </div>
            </div>
            <div className="flex flex-col">
              <div className="text-xl font-bold">List of user accounts</div>
              <div className="text-sm font-bold text-accent-900 bg-accent-400 border border-accent-500 rounded-full px-2 w-max">
                {
                  this.props.selected_development_partner
                    .development_partner_name
                }
              </div>
            </div>
          </div>
          {/* Body */}
          <div className="mt-3 bg-white rounded p-2">
            {this.state.loading === true ||
            this.props.accounts === null ||
            this.props.loading === true ? (
              <div className="flex flex-col items-center justify-center text-center p-8">
                <Loading />
                <div className="font-light text-center">
                  Loading, please wait...
                </div>
              </div>
            ) : (
              <div>
                {this.props.accounts.length > 0 && (
                  <div className="">
                    <input
                      type="search"
                      className="px-3 py-2 font-bold border border-gray-400 w-full rounded"
                      placeholder="Search..."
                      value={this.state.search_data}
                      onChange={(e) =>
                        this.setState({
                          search_data: e.target.value,
                          error: "",
                          success: "",
                        })
                      }
                    />
                  </div>
                )}
                {/* Table */}
                {this.props.accounts.length === 0 && (
                  <div>
                    <div className="flex flex-col items-center justify-center text-center bg-gray-100 rounded-md my-3 p-5">
                      <div className="text-xl font-bold">Add new account</div>
                      <div className="text-sm text-gray-500">
                        Click the following button to add an account under{" "}
                        {
                          this.props.selected_development_partner
                            .development_partner_name
                        }
                      </div>
                      <div className="mt-2">
                        <Button
                          theme="accent"
                          onClick={this.props.onAddNewAccount}
                          className="animate__animated animate__zoomIn"
                        >
                          Create new account
                        </Button>
                      </div>
                    </div>
                  </div>
                )}
                {this.props.accounts.length > 0 && (
                  <div>
                    {search(this.props.accounts, this.state.search_data)
                      .length === 0 ? (
                      <div className="text-base font-light p-3">
                        No result found!
                      </div>
                    ) : (
                      <div className="mt-2">
                        <table className="w-full text-left text-sm">
                          <thead>
                            <tr className="border-b border-gray-300">
                              <th className="px-3 py-2">#</th>
                              <th className="px-3 py-2">First name</th>
                              <th className="px-3 py-2">Last name</th>
                              <th className="px-3 py-2">National ID</th>
                              <th className="px-3 py-2">Phone number</th>
                              <th className="px-3 py-2">Email</th>
                              <th className="px-3 py-2">Role</th>
                            </tr>
                          </thead>
                          <tbody>
                            {(
                              search(
                                this.props.accounts,
                                this.state.search_data
                              ) as DPAccountInterface[]
                            ).map((item, i) => (
                              <tr
                                key={i + 1}
                                className={`${
                                  i % 2 !== 0 ? "bg-gray-100" : "bg-white"
                                }`}
                              >
                                <td className="px-3 py-2">{i + 1}</td>
                                <td className="px-3 py-2">{item.first_name}</td>
                                <td className="px-3 py-2">{item.last_name}</td>
                                <td className="px-3 py-2">{item.nid}</td>
                                <td className="px-3 py-2">
                                  {item.phone_numbers}
                                </td>
                                <td className="px-3 py-2">{item.email}</td>
                                <td className="px-3 py-2">{item.role}</td>
                              </tr>
                            ))}
                          </tbody>
                        </table>
                      </div>
                    )}
                  </div>
                )}
              </div>
            )}
          </div>
        </div>
      </div>
    );
  }
}
