import React, { Component, Fragment } from "react";
import { BiReset } from "react-icons/bi";
import { BsFillInfoCircleFill } from "react-icons/bs";
import { HiShieldCheck } from "react-icons/hi";
import { IoIosArrowDown, IoIosCloseCircle } from "react-icons/io";
import { MdClose } from "react-icons/md";
import { RiTimerFlashFill } from "react-icons/ri";
import { connect } from "react-redux";
import { Basic, FC_GetAllSystemRoles } from "../../actions";
import {
  DPAccountInterface,
  DPMembershipApplicationGetInterface,
  DPMembershipApplicationStatus,
  DPMembershipApplicationStatusList,
  GetAllDPListByStatusInterface,
  GetDPAccountsList,
  GetDPListByStatus,
} from "../../actions/dp_membership_application";
import Loading from "../../components/Loading/Loading";
import Modal, { ModalSize, Themes } from "../../components/Modal/Modal";
import SelectCustom from "../../components/SelectCustom/SelectCustom";
import { StoreState } from "../../reducers";
import { search } from "../../utils/functions";
import { CreateAccount } from "../DPAccount/CreateAccount";
import { DPAccountsList } from "../DPAccount/DPAccountsList";
import { DPApplicationDetails } from "./DPApplicationDetails";
import { ValidateItem } from "./ValidateItem";

interface DPListProps {
  basic: Basic;
  FC_GetAllSystemRoles: (
    callBack: (status: boolean, msg: string) => void
  ) => void;
}
interface DPListState {
  loading: boolean;
  data: GetAllDPListByStatusInterface | null;
  success: string;
  error: string;
  selected_status: DPMembershipApplicationStatus;
  open_select_area: boolean;
  open_select_category: boolean;
  selected_sub_area: { id: string; name: string } | null;
  selected_category: { id: string; name: string } | null;
  search_value: string;
  preview: DPMembershipApplicationGetInterface | null;
  openValidation: DPMembershipApplicationGetInterface | null;
  openRegisterAccount: DPMembershipApplicationGetInterface | null;
  openViewAccountsList: DPMembershipApplicationGetInterface | null;
  accounts: DPAccountInterface[] | null;
}

class _DPList extends Component<DPListProps, DPListState> {
  constructor(props: DPListProps) {
    super(props);

    this.state = {
      loading: false,
      data: null,
      success: "",
      error: "",
      selected_status: DPMembershipApplicationStatus.PENDING,
      open_select_area: false,
      open_select_category: false,
      selected_sub_area: null,
      selected_category: null,
      search_value: "",
      preview: null,
      openValidation: null,
      openRegisterAccount: null,
      openViewAccountsList: null,
      accounts: [],
    };
  }
  // Get DP Applications
  FC_GetDPApplications = (
    status: DPMembershipApplicationStatus,
    register_account: DPMembershipApplicationGetInterface | null
  ) => {
    this.setState({
      loading: true,
      error: "",
      success: "",
      open_select_area: false,
      open_select_category: false,
      selected_category: null,
      selected_sub_area: null,
    });
    GetDPListByStatus(
      status,
      (
        loading: boolean,
        res: {
          type: "success" | "error";
          msg: string;
          data: GetAllDPListByStatusInterface | null;
        } | null
      ) => {
        this.setState({ loading: loading });
        if (res !== null && res.type === "success") {
          if (register_account !== null && res.data !== null) {
            const selected_data = res.data.dp_application.find(
              (itm) =>
                itm.id.toLocaleLowerCase() ===
                register_account.id.toLocaleLowerCase()
            );
            console.log("Lats: ", selected_data);
            if (selected_data !== undefined) {
              // Request to register an account
              this.setState({ openRegisterAccount: selected_data });
            }
          }
          this.setState({
            data: res.data,
            error: "",
            success: res.msg,
            loading: false,
            selected_status: status,
            openValidation: null,
          });
        }
        if (res !== null && res.type === "error") {
          this.setState({
            data: null,
            success: "",
            error: res.msg,
            loading: false,
            openValidation: null,
          });
        }
      }
    );
  };

  GetStatusIcon = (status: DPMembershipApplicationStatus) => {
    if (status === DPMembershipApplicationStatus.PENDING) {
      return <RiTimerFlashFill className="text-4xl" />;
    }
    if (status === DPMembershipApplicationStatus.APPROVED) {
      return <HiShieldCheck className="text-4xl" />;
    }
    if (status === DPMembershipApplicationStatus.REJECTED) {
      return <IoIosCloseCircle className="text-4xl" />;
    } else {
      return <BsFillInfoCircleFill className="text-3xl m-1" />;
    }
  };

  GetStatusColor = (status: DPMembershipApplicationStatus) => {
    if (status === DPMembershipApplicationStatus.PENDING) {
      return "accent";
    }
    if (status === DPMembershipApplicationStatus.APPROVED) {
      return "accent";
    }
    if (status === DPMembershipApplicationStatus.REJECTED) {
      return "accent";
    } else {
      return "accent";
    }
  };

  GetDataByOrgCategory = (
    data: DPMembershipApplicationGetInterface[],
    category_id: string
  ) =>
    data.filter(
      (itm) => itm.organization_category_id.toString() === category_id
    );

  GetDataBySubArea = (
    data: DPMembershipApplicationGetInterface[],
    sub_area_id: string
  ) => {
    const response: DPMembershipApplicationGetInterface[] = [];
    for (const dp of data) {
      const test = dp.subareas.find(
        (itm) => itm.sub_area_id.toString() === sub_area_id.toString()
      );
      if (test !== undefined) {
        response.push(dp);
      }
    }
    return response;
  };

  FC_GetDataCategories = () => {
    if (this.state.data === null) {
      return [];
    }
    const response: { id: string; name: string }[] = [];
    for (const item of this.state.data.dp_application) {
      if (
        response.find(
          (itm) =>
            itm.id.toString() === item.organization_category_id.toString()
        ) === undefined
      ) {
        response.push({
          id: item.organization_category_id.toString(),
          name: item.organization_category,
        });
      }
    }
    return response;
  };

  GetDataByStatus = (
    data: DPMembershipApplicationGetInterface[],
    status: DPMembershipApplicationStatus
  ) => data.filter((itm) => itm.status === status);

  GetSubAreas = () => {
    if (this.state.data === null) {
      return [];
    }
    const response: { id: string; name: string }[] = [];
    for (const dp of this.state.data.dp_application) {
      for (const item of dp.subareas) {
        if (
          response.find(
            (itm) => itm.id.toString() === item.sub_area_id.toString()
          ) === undefined
        ) {
          response.push({
            id: item.sub_area_id.toString(),
            name: `${item.area_name}-${item.subarea_name}`,
          });
        }
      }
    }
    return response;
  };

  GetFilteredData = () => {
    if (this.state.data === null) {
      return [];
    }
    var response: DPMembershipApplicationGetInterface[] =
      this.state.data.dp_application;
    if (this.state.selected_category !== null) {
      response = this.GetDataByOrgCategory(
        response,
        this.state.selected_category.id
      );
    }
    if (this.state.selected_sub_area !== null) {
      response = this.GetDataBySubArea(
        response,
        this.state.selected_sub_area.id
      );
    }
    if (this.state.search_value !== "") {
      response = search(
        response,
        this.state.search_value
      ) as DPMembershipApplicationGetInterface[];
    }
    return response;
  };

  GetTotalByStatus = (status: DPMembershipApplicationStatus) => {
    if (this.state.data === null) {
      return 0;
    }
    if (status === DPMembershipApplicationStatus.PENDING) {
      return this.state.data.PENDING;
    }
    if (status === DPMembershipApplicationStatus.APPROVED) {
      return this.state.data.APPROVED;
    }
    if (status === DPMembershipApplicationStatus.REJECTED) {
      return this.state.data.REJECTED;
    } else {
      return this.state.data.APPEAL;
    }
  };

  // Get DP Accounts
  FC_GetAccounts = (selected_development_partner: string) => {
    this.setState({ loading: true });
    GetDPAccountsList(
      selected_development_partner,
      (
        loading: boolean,
        res: {
          status: "success" | "error";
          msg: string;
          data: DPAccountInterface[] | null;
        } | null
      ) => {
        this.setState({ loading: loading });
        if (res !== null && res.status === "success") {
          this.setState({
            accounts: res.data,
            loading: false,
            error: "",
            success: "",
          });
        }
        if (res !== null && res.status === "error") {
          this.setState({
            accounts: [],
            loading: false,
            error: res.msg,
            success: "",
          });
        }
      }
    );
  };

  componentDidMount = () => {
    if (this.state.data === null) {
      this.FC_GetDPApplications(this.state.selected_status, null);
    }
  };

  render() {
    return (
      <Fragment>
        <div className="container mx-auto p-3">
          {/* Top menu bar */}
          <div className="bg-white rounded-md p-3">
            <div className="grid grid-cols-12 gap-2">
              <div className="col-span-6">
                <div className="flex flex-col pl-2">
                  <div className="text-2xl font-extrabold">
                    Training providers{" "}
                    <span className="text-sm font-bold px-2 rounded-md bg-primary-100 text-primary-800">
                      {this.state.selected_status}
                    </span>
                  </div>
                  <div className="text-sm text-gray-500">
                    Training provider registration approval
                  </div>
                </div>
              </div>
              <div className="col-span-6 flex flex-row items-center justify-end w-full gap-2">
                {DPMembershipApplicationStatusList.map((item, i) =>
                  this.state.data === null || this.state.loading === true ? (
                    <div
                      key={i + 1}
                      className="w-full bg-gray-100 rounded-md h-12"
                    ></div>
                  ) : (
                    <div
                      key={i + 1}
                      className={`px-1 pr-4 rounded-md flex flex-row items-center gap-2 ${
                        this.state.selected_status === item.key
                          ? "bg-accent-400 text-accent-900 animate__animated animate__fadeIn py-1"
                          : "bg-gray-100 hover:bg-accent-400 border-l-2 border-gray-100 py-1"
                      } cursor-pointer group`}
                      onClick={() => this.FC_GetDPApplications(item.key, null)}
                    >
                      <div>
                        <div
                          className={`rounded-md ${
                            this.state.selected_status === item.key
                              ? `bg-white text-${this.GetStatusColor(
                                  item.key
                                )}-900 font-bold border border-accent-800`
                              : `text-gray-300 group-hover:text-${this.GetStatusColor(
                                  item.key
                                )}-900 bg-white`
                          } text-2xl font-extrabold px-1 py-1`}
                        >
                          {this.GetStatusIcon(item.key)}
                        </div>
                      </div>
                      <div className="flex flex-col">
                        <div
                          className={`text-sm ${
                            this.state.selected_status === item.key
                              ? "font-base"
                              : "font-light"
                          }`}
                        >
                          {item.title}
                        </div>
                        <div className="text-2xl font-extrabold -mt-1">
                          {this.GetTotalByStatus(item.key)}
                        </div>
                      </div>
                    </div>
                  )
                )}
              </div>
            </div>
          </div>
          {this.state.loading === true ? (
            <div className="flex flex-col items-center justify-center bg-white rounded-md mt-2 py-5">
              <Loading />
              <div className="text-center text-xl font-light text-gray-500">
                Loading, please wait...
              </div>
            </div>
          ) : (
            <div className="bg-white rounded-md mt-2 py-2">
              <div className="flex flex-row items-center justify-between mb-3 mx-2 gap-3">
                <div className="relative w-1/3">
                  {this.state.open_select_category === false ? (
                    <div
                      onClick={() =>
                        this.setState({
                          open_select_category:
                            !this.state.open_select_category,
                        })
                      }
                      className="border border-gray-400 rounded px-3 py-2 w-full flex flex-row items-center justify-between gap-2 cursor-pointer hover:border-accent-900 hover:text-accent-900"
                    >
                      <div className="w-52 truncate">
                        {this.state.selected_category === null
                          ? "Search by intervention area"
                          : this.state.selected_category.name}
                      </div>
                      <div>
                        <IoIosArrowDown />
                      </div>
                    </div>
                  ) : (
                    <div
                      onClick={() =>
                        this.setState({ open_select_category: false })
                      }
                      className="font-bold flex flex-row items-center gap-4 cursor-pointer hover:text-accent-900"
                    >
                      <span>Select intervention area</span>
                      <div>
                        {this.state.selected_category !== null ? (
                          <div
                            onClick={() =>
                              this.setState({
                                selected_category: null,
                                open_select_category: false,
                              })
                            }
                            className="font-normal text-sm cursor-pointer px-2 py-1 rounded-md bg-accent-400 text-accent-900 flex flex-row items-center justify-center gap-2"
                          >
                            <div>
                              <BiReset className="text-xl" />
                            </div>
                            <span>Reset</span>
                          </div>
                        ) : (
                          <MdClose />
                        )}
                      </div>
                    </div>
                  )}
                  {this.state.open_select_category === true && (
                    <div className="w-/12 absolute mt-2 bg-white">
                      <SelectCustom
                        loading={false}
                        id={"id"}
                        title={"name"}
                        close={() =>
                          this.setState({ open_select_category: false })
                        }
                        data={this.FC_GetDataCategories()}
                        click={(response: { id: string; name: string }) => {
                          this.setState({
                            selected_category: response,
                            open_select_category: false,
                          });
                        }}
                      />
                    </div>
                  )}
                </div>
                {/* ------------------------------- */}
                <div className="relative w-1/3">
                  {this.state.open_select_area === false ? (
                    <div
                      onClick={() =>
                        this.setState({
                          open_select_area: !this.state.open_select_area,
                        })
                      }
                      className="border border-gray-400 rounded px-3 py-2 w-full flex flex-row items-center justify-between gap-2 cursor-pointer hover:border-accent-900 hover:text-accent-900"
                    >
                      <div className="w-52 truncate">
                        {this.state.selected_sub_area === null
                          ? "Search by sub area"
                          : this.state.selected_sub_area.name}
                      </div>
                      <div>
                        <IoIosArrowDown />
                      </div>
                    </div>
                  ) : (
                    <div
                      onClick={() => this.setState({ open_select_area: false })}
                      className="font-bold flex flex-row items-center gap-4 cursor-pointer hover:text-accent-900"
                    >
                      <span>Select sub area</span>
                      <div>
                        {this.state.selected_sub_area !== null ? (
                          <div
                            onClick={() =>
                              this.setState({
                                selected_sub_area: null,
                                open_select_area: false,
                              })
                            }
                            className="font-normal text-sm cursor-pointer px-2 py-1 rounded-md bg-accent-400 text-accent-900 flex flex-row items-center justify-center gap-2"
                          >
                            <div>
                              <BiReset className="text-xl" />
                            </div>
                            <span>Reset</span>
                          </div>
                        ) : (
                          <MdClose />
                        )}
                      </div>
                    </div>
                  )}
                  {this.state.open_select_area === true && (
                    <div className="w-/12 absolute mt-2 bg-white">
                      <SelectCustom
                        loading={false}
                        id={"id"}
                        title={"name"}
                        close={() => this.setState({ open_select_area: false })}
                        data={this.GetSubAreas()}
                        click={(response: { id: string; name: string }) => {
                          this.setState({
                            selected_sub_area: response,
                            open_select_area: false,
                          });
                        }}
                      />
                    </div>
                  )}
                </div>
                <input
                  type="search"
                  className="px-3 py-2 rounded w-1/3 border border-gray-400"
                  placeholder="Search..."
                  value={this.state.search_value}
                  onChange={(e) =>
                    this.setState({ search_value: e.target.value })
                  }
                />
              </div>
              <div className="w-full overflow-x-auto">
                <table className="w-full text-sm text-left">
                  <thead className="bg-primary-100 text-primary-800 border-4 border-white">
                    <tr>
                      <th className="px-3 py-2">#</th>
                      <th className="px-3 py-2">Organization</th>
                      <th className="px-3 py-2">Category</th>
                      <th className="px-3 py-2">Phone</th>
                      <th className="px-3 py-2">Email</th>
                      <th className="px-3 py-2">Location</th>
                      <th className="px-3 py-2">Status</th>
                    </tr>
                  </thead>
                  <tbody>
                    {this.state.data === null ? (
                      <tr>
                        <td
                          colSpan={7}
                          className="text-xl px-4 py-3 font-light text-center"
                        >
                          Loading, please wait...
                        </td>
                      </tr>
                    ) : this.GetFilteredData().length === 0 ? (
                      <tr>
                        <td
                          colSpan={7}
                          className="text-xl px-4 py-3 font-light text-center"
                        >
                          No data found!
                        </td>
                      </tr>
                    ) : (
                      this.GetFilteredData().map((item, i) => (
                        <tr
                          key={i + 1}
                          className={`${
                            i % 2 !== 0 ? "bg-gray-100" : "bg-white"
                          } hover:bg-accent-400 hover:text-accent-900 cursor-pointer`}
                          onClick={() => this.setState({ preview: item })}
                        >
                          <td className="px-3 py-2">{i + 1}</td>
                          <td className="px-3 py-2">
                            {item.development_partner_name}
                          </td>
                          <td className="px-3 py-2">
                            {item.organization_category}
                          </td>
                          <td className="px-3 py-2">{item.phone}</td>
                          <td className="px-3 py-2">{item.email}</td>
                          <td className="px-3 py-2">{item.location}</td>
                          <td>
                            <div
                              className={`${
                                item.status ===
                                DPMembershipApplicationStatus.APPROVED
                                  ? "bg-green-100 text-green-700"
                                  : item.status ===
                                    DPMembershipApplicationStatus.PENDING
                                  ? "bg-accent-400 text-accent-900"
                                  : item.status ===
                                    DPMembershipApplicationStatus.REJECTED
                                  ? "bg-red-100 text-red-700"
                                  : "bg-accent-400 text-black"
                              } px-1 py-0 w-max rounded-md text-xs`}
                            >
                              {item.status}
                            </div>
                          </td>
                        </tr>
                      ))
                    )}
                  </tbody>
                </table>
              </div>
            </div>
          )}
        </div>
        {this.state.preview !== null && (
          <Modal
            backDrop={true}
            theme={Themes.default}
            close={() => this.setState({ preview: null })}
            backDropClose={false}
            widthSizeClass={ModalSize.extraLarge}
            displayClose={false}
            padding={{
              title: undefined,
              body: undefined,
              footer: undefined,
            }}
          >
            <div>
              <DPApplicationDetails
                data={this.state.preview}
                selected_status={this.state.selected_status}
                onClose={() => this.setState({ preview: null })}
                openValidation={() => {
                  this.setState({
                    openValidation: this.state.preview,
                    preview: null,
                  });
                }}
                onRegisterAccount={(
                  data: DPMembershipApplicationGetInterface
                ) => {
                  this.setState({
                    preview: null,
                    openValidation: null,
                    openRegisterAccount: data,
                  });
                }}
                onViewAccountsList={(
                  data: DPMembershipApplicationGetInterface
                ) => {
                  this.FC_GetAccounts(data.id);
                  this.setState({ openViewAccountsList: data, preview: null });
                }}
              />
            </div>
          </Modal>
        )}
        {this.state.openValidation !== null && (
          <Modal
            backDrop={true}
            theme={Themes.default}
            close={() => this.setState({ openValidation: null })}
            backDropClose={false}
            widthSizeClass={ModalSize.medium}
            displayClose={false}
            padding={{
              title: undefined,
              body: undefined,
              footer: undefined,
            }}
          >
            <div>
              <ValidateItem
                data={this.state.openValidation}
                selected_status={this.state.selected_status}
                onClose={() => {
                  this.setState({
                    preview: this.state.openValidation,
                    openValidation: null,
                  });
                }}
                onReloadList={(status: DPMembershipApplicationStatus) => {
                  this.FC_GetDPApplications(status, null);
                }}
                onRegister={(newData: boolean) => {
                  const temp_data = this.state.openValidation;
                  console.log("TEST: ", newData, temp_data);
                  if (newData === true) {
                    this.FC_GetDPApplications(
                      DPMembershipApplicationStatus.APPROVED,
                      temp_data
                    );
                  } else {
                    this.setState({ openRegisterAccount: temp_data });
                  }
                }}
              />
            </div>
          </Modal>
        )}
        {/* Register new account */}
        {this.state.openRegisterAccount !== null && (
          <Modal
            backDrop={true}
            theme={Themes.default}
            close={() => this.setState({ openRegisterAccount: null })}
            backDropClose={false}
            widthSizeClass={ModalSize.extraLarge}
            displayClose={false}
            padding={{
              title: undefined,
              body: undefined,
              footer: undefined,
            }}
          >
            <div>
              <CreateAccount
                developmentPartner={this.state.openRegisterAccount}
                onCancel={() =>
                  this.setState({
                    preview: this.state.openRegisterAccount,
                    openValidation: null,
                    openRegisterAccount: null,
                  })
                }
                onSuccess={(development_partner_id: string) =>
                  this.FC_GetAccounts(development_partner_id)
                }
              />
            </div>
          </Modal>
        )}
        {this.state.openViewAccountsList !== null && (
          <Modal
            backDrop={true}
            theme={Themes.default}
            close={() => this.setState({ openViewAccountsList: null })}
            backDropClose={false}
            widthSizeClass={ModalSize.extraLarge}
            displayClose={false}
            padding={{
              title: undefined,
              body: undefined,
              footer: undefined,
            }}
          >
            <div>
              <DPAccountsList
                onCancel={() =>
                  this.setState({
                    preview: this.state.openViewAccountsList,
                    openValidation: null,
                    openRegisterAccount: null,
                    openViewAccountsList: null,
                  })
                }
                selected_development_partner={this.state.openViewAccountsList}
                FC_GetAccounts={this.FC_GetAccounts}
                accounts={this.state.accounts}
                onAddNewAccount={() => {
                  this.setState({
                    openRegisterAccount: this.state.openViewAccountsList,
                    openValidation: null,
                    openViewAccountsList: null,
                    preview: null,
                  });
                }}
                loading={this.state.loading}
              />
            </div>
          </Modal>
        )}
      </Fragment>
    );
  }
}

const mapStateToProps = ({ basic }: StoreState): { basic: Basic } => {
  return { basic: basic };
};

export const DPList = connect(mapStateToProps, { FC_GetAllSystemRoles })(
  _DPList
);
