import React, { Component } from "react";
import { BsArrowLeft } from "react-icons/bs";
import { MdClose } from "react-icons/md";
import { FC_GetUserProfile, UserDetailsByNID } from "../../actions";
import {
  AccountLevels,
  AccountRoleInterface,
  CreateDPAccountInterface,
  DPMembershipApplicationGetInterface,
  FC_CreateDPAccount,
  GetAccountRolesByLevel,
} from "../../actions/dp_membership_application";
import Alert from "../../components/Alert/Alert";
import Button from "../../components/Buttons/Button";
import InputSelect from "../../components/Inputs/InputSelect";
import TextInput from "../../components/Inputs/TextInput";
import Loading from "../../components/Loading/Loading";
import {
  FC_GetSettingType,
  TrainingSettings_Interface,
  TRNG_TRAINER_INSTITUTION_VIEW_URL,
} from "../actions/training.settings.action";

interface CreateAccountProps {
  developmentPartner: DPMembershipApplicationGetInterface;
  onCancel: () => void;
  onSuccess: (development_partner_id: string) => void;
}
interface CreateAccountState {
  loading: boolean;
  loading_data: boolean;
  error: {
    target:
      | "main"
      | "nid"
      | "first_name"
      | "middle_name"
      | "last_name"
      | "full_name"
      | "phone_number"
      | "email"
      | "role_id"
      | "development_partner_id"
      | "institution_id"
      | "searchUser";
    msg: string;
  } | null;
  success: string;
  nid: string;
  first_name: string;
  middle_name: string;
  last_name: string;
  full_name: string;
  phone_number: string;
  email: string;
  role_id: number | null;
  development_partner_id: string;
  account_levels: AccountRoleInterface[] | null;
  trainer_institutions: TrainingSettings_Interface[] | null;
  institution_id: string;
  searchUser: string;
  searchLoading: boolean;
}

export class CreateAccount extends Component<
  CreateAccountProps,
  CreateAccountState
> {
  constructor(props: CreateAccountProps) {
    super(props);

    this.state = {
      loading: false,
      error: null,
      success: "",
      development_partner_id: this.props.developmentPartner.id,
      email: "",
      first_name: "",
      full_name: "",
      last_name: "",
      middle_name: "",
      nid: "",
      phone_number: "",
      role_id: null,
      loading_data: false,
      account_levels: null,
      trainer_institutions: null,
      institution_id: "",
      searchUser: "",
      searchLoading: false,
    };
  }

  componentDidMount(): void {
    if (
      this.state.account_levels === null ||
      this.state.account_levels.length === 0
    ) {
      this.setState({ loading_data: true });
      //   this.props.FC_GetAllSystemRoles((status: boolean, msg: string) => {
      //     if (status === true) {
      //       this.setState({ loading_data: false });
      //     } else {
      //       this.setState({
      //         loading_data: false,
      //         error: { target: "main", msg: msg },
      //       });
      //     }
      //   });
      GetAccountRolesByLevel(
        AccountLevels.TRAINING,
        (
          loading: boolean,
          res: {
            status: "success" | "error";
            msg: string;
            data: AccountRoleInterface[];
          } | null
        ) => {
          this.setState({ loading_data: loading });
          if (res !== null && res.status === "success") {
            this.setState({
              account_levels: res.data,
              loading: false,
              loading_data: false,
              error: null,
            });
          }
          if (res !== null && res.status === "error") {
            this.setState({
              account_levels: [],
              loading: false,
              loading_data: false,
              error: { target: "main", msg: res.msg },
            });
          }
        }
      );
    }
  }
  cleanForm = () => {
    this.setState({
      email: "",
      first_name: "",
      full_name: "",
      last_name: "",
      middle_name: "",
      nid: "",
      phone_number: "",
      role_id: null,
    });
  };
  SubmitData = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    // Validation
    if (this.state.first_name === "") {
      return this.setState({
        error: { target: "first_name", msg: "Please fill the first name" },
      });
    }
    if (this.state.last_name === "") {
      return this.setState({
        error: { target: "last_name", msg: "Please fill the last name" },
      });
    }
    if (this.state.nid === "") {
      return this.setState({
        error: { target: "nid", msg: "Please fill national ID" },
      });
    }
    if (this.state.phone_number === "") {
      return this.setState({
        error: { target: "phone_number", msg: "Please fill phone number" },
      });
    }
    // if (this.state.email === "") {
    //   return this.setState({
    //     error: { target: "email", msg: "Please fill the email" },
    //   });
    // }
    if (this.state.role_id === null || isNaN(this.state.role_id)) {
      return this.setState({
        error: { target: "role_id", msg: "Please select the role" },
      });
    }
    if (this.state.role_id === 32 && this.state.institution_id === "") {
      return this.setState({
        error: {
          target: "institution_id",
          msg: "Please select the institution",
        },
      });
    }
    // submit data
    const data: CreateDPAccountInterface = {
      development_partner_id: this.state.development_partner_id,
      email: this.state.email,
      first_name: this.state.first_name,
      full_name:
        this.state.first_name +
        " " +
        this.state.middle_name +
        " " +
        this.state.last_name,
      last_name: this.state.last_name,
      middle_name: this.state.middle_name,
      nid: this.state.nid,
      phone_number: this.state.phone_number,
      role_id: this.state.role_id,
      institution_id:
        this.state.institution_id === "" ? "14" : this.state.institution_id,
    };
    this.setState({ loading: true });
    FC_CreateDPAccount(
      data,
      (
        loading: boolean,
        res: { status: "success" | "error"; msg: string } | null
      ) => {
        this.setState({ loading: loading });
        if (res !== null && res.status === "success") {
          this.setState({ success: res.msg, error: null, loading: false });
          this.cleanForm();
          this.props.onSuccess(this.state.development_partner_id);
        }
        if (res !== null && res.status === "error") {
          this.setState({
            error: { target: "main", msg: res.msg },
            success: "",
            loading: false,
          });
        }
      }
    );
  };
  GetTrainerInstitutions = () => {
    this.setState({ loading: true });
    FC_GetSettingType(
      `${TRNG_TRAINER_INSTITUTION_VIEW_URL}`,
      (status, data, error) => {
        this.setState({
          loading: false,
          trainer_institutions: status ? data : [],
          error: !status
            ? {
                target: "main",
                msg: `Error: ${error}`,
              }
            : null,
        });
      }
    );
  };
  searchUserFn = () => {
    if (this.state.searchUser === "") {
      return this.setState({
        error: {
          target: "searchUser",
          msg: "Please fill phone number or NID",
        },
      });
    }
    this.setState({ searchLoading: true });
    FC_GetUserProfile(
      this.state.searchUser,
      (status: boolean, data: UserDetailsByNID | null, msg: string) => {
        console.log(data);
        if (status === true && data !== null) {
          this.setState({
            first_name: data.first_name || "",
            email: data.email || "",
            last_name: data.last_name || "",
            middle_name: data.middle_name || "",
            full_name: data.full_name || "",
            phone_number: data.phone_numbers || "",
            nid: data.nid || "",
          });
          if (data.role_id !== null) {
            this.setState({ role_id: parseInt(data.role_id) });
          }
          this.setState({ searchLoading: false, error: null, success: "" });
        } else {
          console.log("Test msg: ", msg);
          this.setState({
            error: { target: "searchUser", msg: "User not found!" },
            searchLoading: false,
            success: "",
          });
        }
      }
    );
  };
  render() {
    if (this.state.loading_data === true) {
      return (
        <div className="p-8 flex flex-col items-center justify-center bg-white rounded-md my-2">
          <Loading />
          <div className="text-lg font-light text-center">
            Loading, please wait...
          </div>
        </div>
      );
    }
    return (
      <div className="bg-white p-2 rounded">
        <div className="bg-gray-100 py-1 flex flex-row items-center gap-2 rounded px-2 animate__animated animate__fadeIn">
          <div>
            <div
              onClick={() => {
                this.state.loading === false && this.props.onCancel();
              }}
              className="bg-white rounded-full flex items-center justify-center h-9 w-9 cursor-pointer hover:bg-accent-900 hover:text-white"
            >
              <BsArrowLeft className="text-2xl" />
            </div>
          </div>
          <div className="flex flex-col">
            <div className="text-xl font-bold">Create new account</div>
            <div className="text-sm font-bold text-accent-900 bg-accent-400 border border-accent-500 rounded-full px-2 w-max">
              {this.props.developmentPartner.development_partner_name}
            </div>
          </div>
        </div>
        <div className="mt-4">
          <div className="flex flex-row items-center gap-2 w-full">
            <input
              type="search"
              className="border-2 border-yellow-800 rounded-md w-full px-3 py-2 bg-gray-100 b"
              placeholder="Search by phone number"
              value={this.state.searchUser}
              disabled={this.state.searchLoading}
              onChange={(e) =>
                this.setState({
                  searchUser: e.target.value,
                  error: null,
                  success: "",
                })
              }
            />
            <Button
              onClick={() => this.searchUserFn()}
              loading={this.state.searchLoading}
            >
              Search
            </Button>
          </div>
          {this.state.error?.target === "searchUser" && (
            <div className="my-2">
              <Alert
                theme="danger"
                onClick={() => this.setState({ error: null })}
              >
                {this.state.error.msg}
              </Alert>
            </div>
          )}
        </div>
        {/* Body */}
        <form onSubmit={this.SubmitData} className="w-full my-3">
          <div className="grid grid-cols-12 gap-2">
            <TextInput
              onChange={(e) => this.setState({ first_name: e.target.value })}
              value={this.state.first_name}
              error={
                this.state.error !== null &&
                this.state.error.target === "first_name"
                  ? this.state.error.msg
                  : ""
              }
              label={"First name"}
              placeholder={"Ex: Damascene"}
              name={""}
              MainContainer_className="col-span-4"
              disabled={this.state.loading}
            />
            <TextInput
              onChange={(e) => this.setState({ middle_name: e.target.value })}
              value={this.state.middle_name}
              error={
                this.state.error !== null &&
                this.state.error.target === "middle_name"
                  ? this.state.error.msg
                  : ""
              }
              label={"Middle name (Optional)"}
              placeholder={""}
              name={""}
              MainContainer_className="col-span-4"
              disabled={this.state.loading}
            />
            <TextInput
              onChange={(e) => this.setState({ last_name: e.target.value })}
              value={this.state.last_name}
              error={
                this.state.error !== null &&
                this.state.error.target === "last_name"
                  ? this.state.error.msg
                  : ""
              }
              label={"Last name "}
              placeholder={"Ex: KAMANA"}
              name={""}
              MainContainer_className="col-span-4"
              disabled={this.state.loading}
            />
            <TextInput
              onChange={(e) => this.setState({ nid: e.target.value })}
              value={this.state.nid}
              error={
                this.state.error !== null && this.state.error.target === "nid"
                  ? this.state.error.msg
                  : ""
              }
              label={"National ID"}
              placeholder={""}
              name={""}
              MainContainer_className="col-span-6"
              disabled={this.state.loading}
            />
            <TextInput
              onChange={(e) => this.setState({ phone_number: e.target.value })}
              value={this.state.phone_number}
              error={
                this.state.error !== null &&
                this.state.error.target === "phone_number"
                  ? this.state.error.msg
                  : ""
              }
              label={"Phone number"}
              placeholder={"07XXXXXXXX"}
              name={""}
              MainContainer_className="col-span-6"
              disabled={this.state.loading}
            />
            <TextInput
              onChange={(e) => this.setState({ email: e.target.value })}
              value={this.state.email}
              error={
                this.state.error !== null && this.state.error.target === "email"
                  ? this.state.error.msg
                  : ""
              }
              label={"Email (optional)"}
              placeholder={"email@gmail.com"}
              name={""}
              MainContainer_className={
                this.state.role_id === 32 ? "col-span-4" : "col-span-6"
              }
              disabled={this.state.loading}
            />
            <InputSelect
              onChange={(e) => {
                this.setState({
                  role_id: parseInt(e.target.value),
                  institution_id:
                    e.target.value !== "32" ? "" : this.state.institution_id,
                });
                if (
                  e.target.value === "32" &&
                  this.state.trainer_institutions === null
                ) {
                  this.GetTrainerInstitutions();
                }
              }}
              value={this.state.role_id === null ? "" : this.state.role_id}
              error={
                this.state.error !== null &&
                this.state.error.target === "role_id"
                  ? this.state.error.msg
                  : ""
              }
              label={"Select role"}
              placeholder={"Role"}
              name={"role_id"}
              data={[
                {
                  title: "Select role",
                  value: "",
                },
                ...(this.state.account_levels === null
                  ? []
                  : this.state.account_levels.map((item) => ({
                      value: item.role_id,
                      title: item.role,
                    }))),
              ]}
              MainContainer_className={
                this.state.role_id === 32 ? "col-span-4" : "col-span-6"
              }
              disabled={this.state.loading}
            />
            {this.state.role_id === 32 && (
              <InputSelect
                onChange={(e) => {
                  this.setState({
                    institution_id: e.target.value,
                    error: null,
                  });
                }}
                value={this.state.institution_id}
                error={
                  this.state.error !== null &&
                  this.state.error.target === "institution_id"
                    ? this.state.error.msg
                    : ""
                }
                label={"Select institution"}
                placeholder={"Institution"}
                name={"institution_id"}
                data={[
                  {
                    title: "Select institution",
                    value: "",
                  },
                  ...(this.state.trainer_institutions === null
                    ? []
                    : this.state.trainer_institutions.map((item) => ({
                        value: item.id || "14",
                        title: item.name,
                      }))),
                ]}
                MainContainer_className={"col-span-4"}
                disabled={this.state.loading}
              />
            )}
          </div>
          {this.state.error !== null && this.state.error.target === "main" && (
            <div
              onClick={() => this.setState({ error: null, success: "" })}
              className="cursor-pointer px-3 py-2 font-bold bg-red-100 text-red-700 flex flex-row items-center justify-between w-full my-2"
            >
              <span>{this.state.error.msg}</span>
              <div>
                <MdClose className="text-2xl" />
              </div>
            </div>
          )}
          {this.state.success !== "" && (
            <div
              onClick={() => this.setState({ error: null, success: "" })}
              className="cursor-pointer px-3 py-2 font-bold bg-green-100 text-green-700 flex flex-row items-center justify-between w-full my-2"
            >
              <span>{this.state.success}</span>
              <div>
                <MdClose className="text-2xl" />
              </div>
            </div>
          )}
          <div className="flex flex-row items-center justify-end gap-2 mt-3">
            {this.state.loading === true && (
              <div>
                <Loading />
              </div>
            )}
            <Button
              onClick={this.props.onCancel}
              type="button"
              theme="default"
              disabled={this.state.loading}
            >
              Go Back
            </Button>
            {/* <Button
              onClick={() => this.cleanForm()}
              type="button"
              theme="default"
              disabled={this.state.loading}
            >
              Reset form
            </Button> */}
            <Button type="submit" theme="accent" disabled={this.state.loading}>
              {this.state.loading === true
                ? "Loading, please wait..."
                : "Register Account"}
            </Button>
          </div>
        </form>
      </div>
    );
  }
}
