import React, { Component } from "react";
import { AiFillQuestionCircle } from "react-icons/ai";
import { BsArrowLeft } from "react-icons/bs";
import { HiShieldCheck } from "react-icons/hi";
import { IoIosCloseCircle, IoMdCloseCircle } from "react-icons/io";
import { MdCheckCircle } from "react-icons/md";
import {
  DPMembershipApplicationGetInterface,
  DPMembershipApplicationStatus,
  DPMembershipApplicationStatusList,
  SubmitDPMembershipApplicationDecision,
} from "../../actions/dp_membership_application";
import Button from "../../components/Buttons/Button";
import TextArea from "../../components/Inputs/TextArea";
import Loading from "../../components/Loading/Loading";

interface ValidateItemProps {
  selected_status: DPMembershipApplicationStatus;
  data: DPMembershipApplicationGetInterface;
  onClose: () => void;
  onReloadList: (status: DPMembershipApplicationStatus) => void;
  onRegister: (newData: boolean) => void;
}
interface ValidateItemState {
  submit_status: DPMembershipApplicationStatus;
  loading: boolean;
  error: {
    target: "main" | "decision_comment";
    msg: string;
  } | null;
  decision_comment: string;
  success: string;
  confirm_submit: boolean;
}

export class ValidateItem extends Component<
  ValidateItemProps,
  ValidateItemState
> {
  constructor(props: ValidateItemProps) {
    super(props);

    this.state = {
      loading: false,
      error: null,
      submit_status: DPMembershipApplicationStatus.APPROVED,
      decision_comment: "",
      success: "",
      confirm_submit: false,
    };
  }
  GetStatusIcon = (status: DPMembershipApplicationStatus) => {
    if (status === DPMembershipApplicationStatus.APPROVED) {
      return <HiShieldCheck className="text-4xl" />;
    }
    if (status === DPMembershipApplicationStatus.REJECTED) {
      return <IoIosCloseCircle className="text-4xl" />;
    } else {
      return <AiFillQuestionCircle className="text-4xl" />;
    }
  };
  submitApplicationStatus = () => {
    this.setState({ confirm_submit: false });
    if (
      this.state.submit_status !== DPMembershipApplicationStatus.APPROVED &&
      this.state.decision_comment === ""
    ) {
      return this.setState({
        error: {
          target: "decision_comment",
          msg: "Please, decision comment is required",
        },
      });
    }
    SubmitDPMembershipApplicationDecision(
      this.props.data.id,
      this.state.decision_comment,
      this.state.submit_status,
      (
        loading: boolean,
        res: { status: "success" | "error"; msg: string } | null
      ) => {
        this.setState({ loading: loading });
        if (res !== null && res.status === "success") {
          return this.setState({ success: res.msg, error: null });
        }
        if (res !== null && res.status === "error") {
          return this.setState({
            error: {
              target: "main",
              msg: res.msg,
            },
          });
        }
      }
    );
  };
  render() {
    if (this.state.loading === true) {
      return (
        <div className="p-8 flex flex-col items-center justify-center">
          <Loading />
          <div className="text-xl font-light text-center">
            Loading, please wait...
          </div>
        </div>
      );
    }
    // Error message (From API)
    if (this.state.error !== null && this.state.error.target === "main") {
      return (
        <div className="bg-white animate__animated animate__zoomIn animate__faster p-5">
          <div className=" flex flex-col items-center justify-center gap-2 text-red-600">
            <div>
              <IoMdCloseCircle className="text-6xl" />
            </div>
            <div className="text-lg font-bold text-black text-center">
              {this.state.error.msg}
            </div>
          </div>
          <div
            className={`flex flex-row items-center justify-${
              this.state.submit_status ===
              DPMembershipApplicationStatus.APPROVED
                ? "center"
                : "center"
            } gap-2 w-full mt-5`}
          >
            <Button
              onClick={() =>
                this.setState({ confirm_submit: false, error: null })
              }
              theme="accent"
            >
              Continue
            </Button>
          </div>
        </div>
      );
    }
    // Success message
    if (this.state.success !== "") {
      return (
        <div className="bg-white animate__animated animate__zoomIn animate__faster p-5">
          <div className=" flex flex-col items-center justify-center gap-2 text-green-600">
            <div>
              <MdCheckCircle className="text-6xl" />
            </div>
            <div className="text-lg font-bold text-black text-center">
              {this.state.success}
            </div>
          </div>
          <div
            className={`flex flex-row items-center justify-${
              this.state.submit_status ===
              DPMembershipApplicationStatus.APPROVED
                ? "center"
                : "center"
            } gap-2 w-full mt-5`}
          >
            {/* {this.state.submit_status ===
              DPMembershipApplicationStatus.APPROVED && (
              <Button
                onClick={() => this.props.onRegister(true)}
                theme="accent"
              >
                Continue with registration
              </Button>
            )} */}
            <Button
              onClick={() => this.props.onReloadList(this.state.submit_status)}
              theme="accent"
            >
              {this.state.submit_status ===
              DPMembershipApplicationStatus.APPROVED
                ? "Continue"
                : "Continue"}
            </Button>
          </div>
        </div>
      );
    }
    const details = this.props.data;
    if (this.state.confirm_submit === true) {
      return (
        <div className="bg-white animate__animated animate__zoomIn animate__faster p-5">
          <div className=" flex flex-row items-center gap-2 ">
            <div>
              <AiFillQuestionCircle className="text-4xl" />
            </div>
            <div className="text-lg font-bold">
              Are you sure! Do you want to{" "}
              <span className="px-1 rounded-md font-bold bg-accent-400 text-accent-900">
                {this.state.submit_status.slice(0, -1)}
              </span>{" "}
              this registration?
            </div>
          </div>
          {this.state.decision_comment !== "" && (
            <div className="bg-gray-100 rounded-md px-3 py-2 w-full my-3 text-left">
              <div className="font-bold text-sm text-gray-500">
                Comment added:
              </div>
              <div className="">{this.state.decision_comment}</div>
            </div>
          )}
          <div
            className={`flex flex-row items-center justify-${
              this.state.submit_status ===
              DPMembershipApplicationStatus.APPROVED
                ? "center"
                : "center"
            } gap-2 w-full mt-5`}
          >
            <Button
              onClick={() => this.setState({ confirm_submit: false })}
              theme="default"
            >
              Cancel
            </Button>
            <Button
              onClick={() => this.submitApplicationStatus()}
              theme="accent"
            >
              Yes, submit
            </Button>
          </div>
        </div>
      );
    }
    return (
      <div className="p-3">
        <div className="flex flex-row items-center justify-between gap-2 border-b pb-4">
          <div className="bg-white rounded-md flex flex-row items-center gap-3">
            <div
              onClick={this.props.onClose}
              className="h-10 w-10 rounded-full bg-accent-400 hover:bg-accent-500 text-accent-900 hover:text-black flex items-center justify-center cursor-pointer"
            >
              <BsArrowLeft className="text-2xl" />
            </div>
            <div>
              <div className="text-xl font-bold">Validate registration</div>
              <div className="text-sm">{details.development_partner_name}</div>
            </div>
          </div>
          <div className="flex flex-row justify-end gap-2">
            <div className="flex flex-col items-end">
              <div className="text-sm px-2">Status</div>
              <div className="bg-accent-400 text-accent-900 font-bold text-sm px-2 rounded-md">
                {this.props.selected_status}
              </div>
            </div>
          </div>
        </div>
        <div>
          <div className=" rounded-md mt-5">
            <div className="">
              <div className="text-lg font-bold mb-2">Choose decision</div>
              <div className="grid grid-cols-12 gap-2">
                {DPMembershipApplicationStatusList.filter(
                  (itm) => itm.key !== DPMembershipApplicationStatus.PENDING
                ).map((item, i) => (
                  <div
                    key={i + 1}
                    className={`cols-span-12 md:col-span-6 lg:col-span-4 rounded-md animate__animated ${
                      this.state.submit_status === item.key
                        ? "bg-accent-400 text-accent-900 font-semibold border border-accent-800 animate__zoomIn animate__faster"
                        : "bg-gray-100 font-light animate___fadeIn"
                    } p-2 flex flex-col items-center justify-center group hover:bg-accent-400 hover:text-accent-900 cursor-pointer`}
                    onClick={() =>
                      this.setState({
                        submit_status: item.key,
                        decision_comment: "",
                        error: null,
                      })
                    }
                  >
                    <div
                      className={` ${
                        this.state.submit_status === item.key
                          ? "text-accent-900"
                          : "text-gray-400"
                      } group-hover:text-accent-900`}
                    >
                      {this.GetStatusIcon(item.key)}
                    </div>
                    <div className="text-base">{item.title}</div>
                  </div>
                ))}
              </div>
              {this.state.submit_status !==
                DPMembershipApplicationStatus.APPROVED && (
                <div className="pt-3">
                  <TextArea
                    onChange={(e) => {
                      this.setState({
                        decision_comment: e.target.value,
                        error: null,
                      });
                    }}
                    value={this.state.decision_comment}
                    error={
                      this.state.error?.target === "decision_comment"
                        ? this.state.error.msg
                        : ""
                    }
                    label={"Decision feedback/comment"}
                    placeholder={""}
                    name={"decision_comment"}
                    Label_className="font-bold"
                  />
                </div>
              )}
              {/* Footer */}
              <div className="flex flex-row items-center justify-end gap-2 mt-5">
                <Button onClick={this.props.onClose} theme="default">
                  <span>Cancel</span>
                </Button>
                <Button
                  onClick={() => {
                    if (
                      this.state.submit_status !==
                        DPMembershipApplicationStatus.APPROVED &&
                      this.state.decision_comment === ""
                    ) {
                      return this.setState({
                        error: {
                          target: "decision_comment",
                          msg: "Please, decision comment is required",
                        },
                      });
                    }
                    this.setState({ confirm_submit: true });
                  }}
                  theme="accent"
                  className={`${
                    this.state.submit_status ===
                    DPMembershipApplicationStatus.APPROVED
                      ? "animate__animated animate__zoomIn animate__faster"
                      : "animate__animated animate__zoomIn animate__faster"
                  }`}
                >
                  <span>
                    {this.state.submit_status ===
                    DPMembershipApplicationStatus.APPROVED
                      ? "Yes, approve"
                      : "Save changes"}
                  </span>
                </Button>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
